<script>
export default {
  name: "Home",
  data() {
    return {
      value1: 0,
      inx: 0,
      myadd: "",
      userinfo: "",
      total: "",
      cdcode: "",
      alltotal:0,
      heyue_addr:'0x6305e9aeD3c89B3186F3fd9E9378412559BCe310'
    };
  },
  created() {
    this.$toast.loading({
      message: this.$t("common.jzz"),
      forbidClick: true,
      duration: 0
    });
    this.value1 = localStorage.getItem("langinx")
      ? parseInt(localStorage.getItem("langinx"))
      : 0;
  },
  mounted() {
    console.log(221121)
    this.url = window.location.href.split("?")[0];
    sessionStorage.setItem("url", window.location.href.split("?")[0]);
    let loca = window.location.href.split("code=")[1]
      ? window.location.href.split("code=")[1].split("&")[0]
      : 0;
    if (this.$route.query.code || this.$route.query.code == 0 || loca) {
      this.cdcode = this.$route.query.code
        ? this.$route.query.code
        : window.location.href.split("code=")[1].split("&")[0];
    }
    window.addEventListener("load", async () => {
      if (window.ethereum) {
        window.web3 = new Web3(ethereum);
        try {
          await ethereum.enable();
        } catch (error) {}
      } else if (window.web3) {
        window.web3 = new Web3(web3.currentProvider);
      } else {
        console.log(
          "Non-Ethereum browser detected. You should consider trying MetaMask!"
        );
        window.web3 = new Web3(
          new Web3.providers.HttpProvider(
            "https://api.infura.io/v1/jsonrpc/ropsten"
          )
        );
      }
      this.startApp();
    });
      if(Contract){
         this.startApp();
      }
  },
  computed: {
    option1() {
      return [
        { text: this.$t("lang.ft"), value: 0 },
        { text: this.$t("lang.ry"), value: 1 },
        { text: this.$t("lang.hy"), value: 2 }
      ];
    }
  },
  methods: {
    startApp() {
      console.log("startApp.......");
      const that = this;
      let heyue_addr = this.heyue_addr;
      let usdt_heyue = "0xee46026fb343831f0550ca10b5f36dabda34d120";
      let uora_heyue = "0x64eb9594009d2a0714a1313bc8010d94a04e3908";
      Contract = new web3.eth.Contract(abi, heyue_addr); //
      Contractusdttoken = new web3.eth.Contract(tokenabi, usdt_heyue); //
      Contractuoratoken = new web3.eth.Contract(tokenabi, uora_heyue); //
      // if (!sessionStorage.getItem("token")) {
        web3.eth.getAccounts(function(err,accounts) {
          console.log("getAccounts accounts: " + accounts);
          that.myadd = accounts;
          sessionStorage.setItem("token", accounts);
          setTimeout(() => {
            that.getuserinfo(accounts); //
          }, 1000);
          setTimeout(() => {
        that.gettotal()
      }, 3000);
        });
      

      //   let a = [];
      //   a.push(sessionStorage.getItem("token"));
      //   that.myadd = a;
      //   that.getuserinfo(a);
      // }

      
    },
    select(inx) {
      this.inx = inx;
    },
    getuserinfo(accounts) {
      console.log(accounts[0]);
      let a = accounts[0];
      const that = this;
      Contract.methods
        .addressToUser(a)
        .call()
        .then(function(params) {
          console.log(params);
          that.userinfo = params;
        });
    },
    gettotal() {
      console.log(111);
      const that = this;
      Contract.methods
        .totalInvestAmount()
        .call()
        .then(function(params) {
          console.log(params);
          that.total = params;
         
          that.getalltotal()
        });
    },
        getalltotal() {
      console.log(111);
      const that = this;
      Contract.methods
        .totalCapacity()
        .call()
        .then(function(params) {
          console.log(params);
          that.alltotal = params;
           that.$toast.clear()
        });
    },
    change(e) {
      let lang = "zh-CN";
      if (e == 1) {
        lang = "Japanese";
      } else if (e == 2) {
        lang = "Korean";
      }
      this.$i18n.locale = lang;
    },
    rg() {
      if (!this.cdcode) {
        return;
      }
      this.$toast.loading({
        message: this.$t("common.jzz"),
        forbidClick: true,
        duration: 0
      });
      let heyue_addr = this.heyue_addr;
      let SELF_ADDR = this.myadd[0];
      console.log(SELF_ADDR);
      const that=this
      let list = [1000000000, 2000000000, 3000000000, 4000000000, 5000000000];
      Contractusdttoken.methods
        .approve(heyue_addr, list[this.inx])
        .send({ from: SELF_ADDR })
        .on("receipt", function(error, receipt) {
          that.submit()
        }).on("error",function(error){
          console.log(error)
            that.$toast.fail(that.$t('common.sqsb'))
        });
    },
    submit() {
      let list = [1000000000, 2000000000, 3000000000, 4000000000, 5000000000];
      
      let SELF_ADDR = this.myadd[0];
      console.log(SELF_ADDR);
      const that=this
      Contract.methods
        .invest(this.cdcode, list[this.inx])
        .send({ from: SELF_ADDR })
        .on("receipt", function(res) {
          console.log(res);
          
          that.$toast.success(that.$t('common.rgcg'))
        }).on("error",function(error){
          that.$toast.fail(that.$t('common.rgsb'))
        });
         
    }
  },
  watch: {
    value1: function(old, newval) {
      if (old != newval) {
        localStorage.setItem("langinx", old);
        let lang = "zh-CN";
        if (old == 1) {
          lang = "Japanese";
        } else if (old == 2) {
          lang = "Korean";
        }

        localStorage.setItem("lang", lang);
      }
    }
  }
};
</script>
