import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'lib-flexible/flexible.js'
Vue.config.productionTip = false
import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)
import { Tabbar, TabbarItem, DropdownMenu, DropdownItem, Progress, List, Toast } from 'vant';
Vue.use(TabbarItem).use(Tabbar).use(DropdownMenu).use(DropdownItem).use(List).use(Progress);
import VueI18n from 'vue-i18n'
Vue.use(Toast);
Vue.use(VueI18n) // 

const i18n = new VueI18n({
    locale: localStorage.getItem('lang') || 'zh-CN', // 
    messages: {
        'zh-CN': require('./common/zh'), // 
        'Korean': require('./common/Korean'), //
        'Japanese': require('./common/Japanese') //
    }

})

new Vue({
    router,
    store,
    i18n,
    render: h => h(App)
}).$mount('#app')