<template>
  <div id="app">
    <router-view />
    <van-tabbar v-model="active" class="tab" style="padding:6px 0">
      <van-tabbar-item v-for="(item,index) in list" :key="index" @click="tab(index,item.name)">
        <!-- <template slot="icon" slot-scope="props"> -->
        <img :src="index==active ? item.act : item.nomal" width="60" height="48" />
        <!-- </template> -->
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0
    };
  },
  created() {
    console.log(sessionStorage.getItem("tabindex"));
    this.active = sessionStorage.getItem("tabindex")
      ? sessionStorage.getItem("tabindex")
      : 0;
  },
  methods: {
    tab(index, name) {
      this.active = index;
      sessionStorage.setItem("tabindex", index);
      this.$router.push(name);
    }
  },
  computed: {
    list() {
      let list = [];
      if (this.$i18n.locale == "zh-CN") {
        list = [
          {
            name: "/",
            nomal: require("./assets/home.png"),
            act: require("./assets/homeact.png")
          },
          {
            name: "/wallet",
            nomal: require("./assets/wallet.png"),
            act: require("./assets/walletact.png")
          },
          {
            name: "/share",
            nomal: require("./assets/share.png"),
            act: require("./assets/shareact.png")
          }
        ];
      } else if (this.$i18n.locale == "Japanese") {
        list = [
          {
            name: "/",
            nomal: require("./assets/home1.png"),
            act: require("./assets/home1act.png")
          },
          {
            name: "/wallet",
            nomal: require("./assets/wallet1.png"),
            act: require("./assets/wallet1act.png")
          },
          {
            name: "/share",
            nomal: require("./assets/share1.png"),
            act: require("./assets/share1act.png")
          }
        ];
      } else {
        list = [
          {
            name: "/",
            nomal: require("./assets/home2.png"),
            act: require("./assets/home2act.png")
          },
          {
            name: "/wallet",
            nomal: require("./assets/wallet2.png"),
            act: require("./assets/wallet2act.png")
          },
          {
            name: "/share",
            nomal: require("./assets/share2.png"),
            act: require("./assets/share2act.png")
          }
        ];
      }
      return list
    }
  }
};
</script>
