  text-align: center;
  color: #2c3e50;
}

#nav {
  padding: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
.tab {
  background: #000000;
  border-top: none;
  img {

    display: block;
  }
}
.van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after{
  border-width: 0;
}
